import React from "react";

export default function Menu({ startInfographic }) {
  const handleYes = () => {
    return startInfographic();
  };

  return (
    <div className="container">
      <h2>Menu</h2>
      <button
        className="button mt-4 px-4 py-1"
        type="button"
        onClick={() => handleYes()}
      >
        <p>Iniciar</p>
      </button>
    </div>
  );
}
